.myModal {
  position: fixed;
  top: 0;
  left:0;
  right: 0;
  bottom: 0;
  display: none;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 100;
}

.myModalContent {
  padding: 25px;
  background-color: #fff;
  border-radius: 15px;
  min-width: 250px;

}

.myModal.active {
  display: flex;
  justify-content: center;
  align-items: center;
}